import axios from 'axios'

import { message } from 'antd'

/*  */

export const getAccount = (data, sucCb) =>{
    axios.post('/v1/chain/get_account', data)
    .then(res => {
      if(!!res){
        if (!!sucCb) {
          sucCb(res.data);
        }
      }
    })
    .catch(err => {
      message.error('');
      console.log(err)
    })
}