import React ,{ Component } from 'react';

import { Card , Row , Col, Spin, Progress} from 'antd';

import './index.scss';



class smallCard extends Component {
    constructor(props) {
        super(props);
        this.state = { 
           loading: true
         };
    }

    loading = () => {
        this.setState({
            loading: false
        })
    }

    componentDidMount(){
    }

    componentDidUpdate(){
        // if(this.state.loading){
        //     this.loading()
        //     console.log('04',this.props)
        // }
    }
    shouldComponentUpdate(nextProps,nextState){ //stateprops
        if(nextProps.data.prices!==''){
            // console.log('01',nextProps,nextState)
            return true
        }else{
            return false
        }
    }

    componentWillReceiveProps(){
        if(this.state.loading){
            this.loading()
            // console.log(',')
        }
    }

    render() {
        return (
            
            <div className = 'card'>
            <Spin spinning = {this.state.loading}>
                <div className = 'cardlist'>
                    <Row gutter={24}>
                    <Col xs={24} sm={12} md={12} lg={6} xl={6}>
                        <Card title={<div style={{textAlign:"left"}}>
                            <span></span>
                            <h3>{this.props.data.head_block_num}</h3>
                            <span>{this.props.data.head_block_producer}</span>
                            </div>} bordered={false}>
                            <p>{this.props.data.head_block_time}</p>
                        </Card>
                    </Col>
                    <Col xs={24} sm={12} md={12} lg={6} xl={6}>
                        <Card title={<div style={{textAlign:"left"}}>
                            <span></span>
                            <h3>{this.props.data.last_irreversible_block_num}</h3>
                            <span>{this.props.data.server_version_string} - {this.props.data.server_version} </span>
                            </div>} bordered={false}>
                            <p>{this.props.data.diff}</p>
                        </Card>
                    </Col>
                    <Col xs={24} sm={12} md={12} lg={6} xl={6}>
                        <Card title={<div style={{textAlign:"left"}}>
                            <span>FO</span>
                            <h3 className ='fibos_eos'>{this.props.data.supply}</h3>
                            <span>{this.props.data.reserve_supply}</span>
                            </div>} bordered={false}>
                            <p className ='fibos_eos'>{this.props.data.vote } { this.props.data.percent }%</p>
                        </Card>
                    </Col>

                    <Col xs={24} sm={12} md={12} lg={6} xl={6}>
                        <Card title={<div style={{textAlign:"left"}}>
                            <span>(GB)</span>
                            <h3>{this.props.data.show}</h3>
                            <span>
                                <Progress percent={this.props.data.percentage} status="active" style = {{width: 200}}/>
                            </span>
                            </div>} bordered={false}>
                            <p>{this.props.data.prices}</p>
                        </Card>
                    </Col>
                    </Row>
                </div>
                </Spin>
            </div>
        );
    }
}

export default smallCard;