import React ,{ Component } from 'react';


import stroage from '../../model/stroage'


import { loginIronman } from '../../model/ironman'
import { newaccount } from './action'

import {
    Form, Input, Button, Checkbox
} from 'antd';

import ToolAssets from '../../components/ToolHead'

class creatAcountForm extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            account_name: '',
            result: '',
            loading: false,
            transfer: false
         };
    }

    
    handleChange = (e) => {
        this.setState({
            transfer: e.target.checked,
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
          if (!err) {
            loginIronman((data, fo) => {
                values.transfer = this.state.transfer
                
                newaccount(fo ,values, data =>{
                    if(data.transaction_id){
                        this.setState({
                            result: data.transaction_id,
                            loading: true
                        })

                    }
                    
                })
            })
          }
        });
      }

    componentDidMount(){
        let account_name = stroage.get('acount')
        if(account_name){
            this.setState({
                account_name: account_name,
            })
        }
    }


    componentWillReceiveProps(){
        let account_name = stroage.get('acount')
        if(account_name){
            this.setState({
                account_name: account_name,
            })
        }else{
            this.setState({
                account_name: '',
            })
        }
    }
    render() {
        const { getFieldDecorator } = this.props.form;

        return (
            <div className = 'tools'>
                <ToolAssets/>
                <div className = 'content'>
                    <h4>Ironman</h4>
                    <Form  onSubmit={this.handleSubmit}>
                        <Form.Item
                        label=""
                        >
                        {getFieldDecorator('creator', {
                            rules: [{
                            required: true, message: '!',
                            }],
                            initialValue: this.state.account_name
                        })(
                            <Input disabled/>
                        )}
                        </Form.Item>

                        <Form.Item
                        label=""
                        >
                        {getFieldDecorator('name', {
                            rules: [{
                            required: true, message: '!',
                            },{
                                validator(rule, value, callback) {
                                    if (value) {
                                        let re=/^[a-z1-5]{12}$/
                                        if (!re.test(value)) {
                                            callback('12a-z1-5')
                                            return
                                        }
                                    }
                                    callback()
                                }
                            }],
                        })(
                            <Input placeholder="12a-z1-5" />
                        )}
                        </Form.Item>

                        <Form.Item
                        label="Owner "
                        >
                        {getFieldDecorator('owner', {
                            rules: [{
                            required: true, message: ' owner !',
                            },{
                                validator(rule, value, callback) {
                                    if (value) {
                                        if (value.indexOf("FO") !== 0) {
                                            callback(' FO')
                                            return
                                        }
                                    }
                                    callback()
                                }
                            }],
                        })(
                            <Input placeholder="'FO'" />
                        )}
                        </Form.Item>

                        <Form.Item
                        label="Active "
                        >
                        {getFieldDecorator('active', {
                            rules: [{
                            required: true, message: ' Active !!',
                            },{
                                validator(rule, value, callback) {
                                    if (value) {
                                        if (value.indexOf("FO") !== 0) {
                                            callback(' FO')
                                            return
                                        }
                                    }
                                    callback()
                                }
                            }],
                        })(
                            <Input placeholder="'FO'" />
                        )}
                        </Form.Item>

                         <Form.Item
                        label=" (FO)"
                        >
                        {getFieldDecorator('stake_net_quantity', {
                            rules: [{
                            required: true, message: '!',
                            },{
                                validator(rule, value, callback) {
                                    if (value) {
                                        if (isNaN(Number(value))) {
                                            callback('')
                                            return
                                        }
                                    }
                                    callback()
                                }
                            }],
                        })(
                            <Input />
                        )}
                        </Form.Item>

                        <Form.Item
                        label=" (FO)"
                        >
                        {getFieldDecorator('stake_cpu_quantity', {
                            rules: [{
                            required: true, message: '!',
                            },{
                                validator(rule, value, callback) {
                                    if (value) {
                                        if (isNaN(Number(value))) {
                                            callback('')
                                            return
                                        }
                                    }
                                    callback()
                                }
                            }],
                        })(
                            <Input />
                        )}
                        </Form.Item>

                        <Form.Item
                        label=" (bytes)"
                        >
                        {getFieldDecorator('bytes', {
                            rules: [{
                            required: true, message: '!',
                            },{
                                validator(rule, value, callback) {
                                    if (value) {
                                        if (isNaN(Number(value))) {
                                            callback('')
                                            return
                                        }
                                    }
                                    callback()
                                }
                            }],
                        })(
                            <Input />
                        )}
                        </Form.Item>

                        <Form.Item>

                            {getFieldDecorator('transfer')(
                                <Checkbox
                                onChange={this.handleChange}
                            >
                                
                                </Checkbox>
                            )}
                            <p></p>
                        </Form.Item>

                        <Form.Item> 
                            <Button type="primary" htmlType="submit"></Button>
                        </Form.Item>
                    </Form>

                    {
                        this.state.loading ? ( <div>
                            <h4></h4>
                            <p className = 'transactionID'>ID<span>{this.state.result}</span></p>   
                        </div>):''
                    }
                </div>

            </div>
        );
    }
}

const index = Form.create({ name: 'creatAcount' })(creatAcountForm);

export default index;