import axios from 'axios'
import { message } from 'antd'
 
 /*  */
 export const getreward = (data,sucCb) => {
    axios.post('/1.0/app/user/getreward', data )
      .then(res => {
        message.success('');
        if(!!res){
          if (!!sucCb) {
            sucCb();
          }
        }
      })
      .catch(err => {
        message.error('');
        console.log(err)
      })
}