import axios from 'axios'

import { message } from 'antd'

/*  */

export const onCreateKey = (data, sucCb) =>{
    axios.post('/1.0/app/token/createkey', {})
    .then(res => {
      if(!!res){
        if (!!sucCb) {
          sucCb(res.data);
        }
      }
    })
    .catch(err => {
      console.log(err)
    })
}

/*  */
export const creatAccount = (data, sucCb) =>{
    axios.post('/1.0/app/token/create', data)
      .then(res => {
        if(res.data.error){
          message.error('')
        }else{
          message.success('');
          if(!!res){
            if (!!sucCb) {
              sucCb(res);
            }
          }
        }
      })
      .catch(err => {
        message.err('');
        console.log(err)
      })
 }